/*============================================================== 
 For all pages 
 ============================================================== */
#main-wrapper {
    width: 100%;
    overflow: hidden;
}

.page-wrapper {
    background: #eef5f9;
    position: relative;
    transition: 0.2s ease-in;
    display: none;
}

.page-wrapper>.page-content {
    padding: 30px;
    min-height: calc(100vh - 116px);
}

.page-breadcrumb {
    padding: 30px 30px 0px;
}

/*******************
 Footer
*******************/
.footer {
    padding: 15px 20px;
}

/*******************
 Stickey inner-left-part
*******************/
.left-part {
    height: calc(100vh - 220px);
    width: 260px;
    position: absolute;
    border-right: 1px solid #e9ecef;
}

.left-part.fixed-left-part {
    position: fixed;
    top: 0px;
    padding-top: 10px;
}

.left-part .show-left-part {
    position: absolute;
    top: 45%;
    right: -41px;
}

.right-part {
    width: calc(100% - 260px);
    height: calc(100vh - 220px);
    overflow: auto;
    margin-left: 260px;
}

.reverse-mode .left-part {
    right: 0;
    border-left: 1px solid #e9ecef;
}

.reverse-mode .show-left-part {
    right: auto;
    left: -41px;
}

.reverse-mode .right-part {
    margin-left: 0px;
    margin-right: 260px;
}

